body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #141414;
  color: #fff;
}

.custom-menu .ant-menu-item {
  font-size: 18px;
  font-weight: 500;
  color: #fff !important;
  padding: 0 20px;
}

.custom-menu .ant-menu-item:hover {
  background-color: #3a3a3a !important;
  color: #fff !important;
  border-bottom: 2px solid white !important;
}

.custom-menu .ant-menu-item-selected {
  background-color: #2e2e2e !important;
  color: #fff !important;
  border-bottom: none !important;
}

.ant-menu-horizontal > .ant-menu-item::after,
.ant-menu-horizontal > .ant-menu-submenu::after {
  border-bottom: none !important;
}

.boka-bord-knapp {
  background-color: #716559;
  border-color: #716559;
  color: #fff;
  font-size: 18px;
  padding: 24px 24px;
  font-weight: 500;
}

.boka-bord-knapp:hover,
.boka-bord-knapp:focus {
  background-color: #5f574d !important;
  border-color: #5f574d !important;
  color: #fff !important;
}

.skicka-bokning-knapp {
  background-color: #716559;
  border-color: #716559;
  color: #fff;
}

.skicka-bokning-knapp:hover,
.skicka-bokning-knapp:focus {
  background-color: #5f574d !important;
  border-color: #5f574d !important;
  color: #fff !important;
}

.mobile-menu-button {
  display: none;
}

/* Mobile-only menu overrides */
@media (max-width: 768px) {
  .desktop-menu {
    display: none !important;
  }

  .mobile-menu-button {
    display: inline-block !important;
  }

  .ant-drawer-body {
    padding: 0 !important;
  }

  .ant-drawer .ant-menu {
    background-color: #1f1f1f !important;
  }

  .ant-drawer .ant-menu-item {
    padding: 16px 24px;
    font-size: 18px;
    color: #fff !important;
  }

  .ant-drawer .ant-menu-item-selected,
  .ant-drawer .ant-menu-item:hover,
  .ant-drawer .ant-menu-item-active {
    background-color: #2e2e2e !important;
    color: #fff !important;
  }

  .ant-drawer .ant-menu-item a {
    color: #fff !important;
  }

  .ant-drawer .ant-menu-item-selected a {
    color: #fff !important;
  }
}
